* {
  box-sizing: border-box;
}

body {
  background: #eee;
  margin: 20px 0;
}

article {
  width: 21cm;
  min-height: 29.7cm;
  background: #fff;
  margin: 0 auto;
  padding: 1.27cm;
  overflow: auto;
}

p {
  margin: 0;
  line-height: 1.5;
}

h1 {
  margin-top: 0;
  font-size: 25px;
}

h3 {
  color: #333;
  margin: 0;
  font-size: 18px;
}

h4, h5, h6 {
  margin: 0;
  font-weight: normal;
}

ol, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

strong {
  color: #f60;
  font-weight: normal;
}

a {
  color: #999;
  border-bottom: 1px solid;
  text-decoration: none;
}

li {
  color: #666;
  margin: .3em 0;
}

.info {
  position: relative;
}

.info > img {
  width: 2.5cm;
  position: absolute;
  top: 0;
  right: 0;
}

.qr-code {
  width: 2.5cm;
  height: 2.5cm;
}

.projects > ol {
  margin-top: 0;
  padding: 0;
  list-style-position: inside;
}

.projects > ol > li {
  margin-bottom: 24px;
}

.projects > ol > li > header, .projects .details {
  justify-content: space-between;
  display: flex;
}

.skills > .wrapper > ul > li {
  margin-bottom: 8px;
  line-height: 1.2;
}

.exp > ol > li {
  margin: 12px 0;
}

.exp header {
  color: #666;
  margin-bottom: 12px;
  display: flex;
}

.exp header > h3 {
  color: #333;
  font-size: 18px;
}

.exp header > h4 {
  margin-left: 1em;
  font-size: 18px;
}

.exp header > span {
  margin-left: auto;
  font-size: 18px;
}

@media (max-width: 500px) {
  body {
    margin: 0;
  }

  article {
    width: auto;
    height: auto;
  }

  .exp header {
    flex-direction: column;
  }

  .exp header > h3, .exp header > h4, .exp header > span {
    margin: 0;
  }
}

@media print {
  article {
    padding: 0;
  }

  .nextPage {
    page-break-before: always;
    margin-top: 1.27cm;
  }
}

@media (max-width: 500px) {
  article {
    width: auto;
    height: auto;
    padding: 16px;
  }
}

